.percent-holder__wrapper {
	position: relative;
  width: 200px;
  margin: 0 auto;

  &::after {
    content: "";
    position: absolute;
    background: url(../../img/loyalty.svg) no-repeat;
    width: 40px;
    height: 45px;
    top: -25px;
    left: calc(50% - 13px);
  }
}

.percent-holder__svg {
	display: block;
	width: 250px;
	height: 250px;
  transform: translateX(-6%);
  margin-top: 25px;
}

.percent-holder__circle {
  fill: none;
	stroke-width: 1;
	stroke-linecap: round;

  &--first {
    stroke: #ececec;
    z-index: 0;
  }

  &--second {
    stroke: #70a84b;
    animation: progress 1s ease-out forwards;
    z-index: 10;
  }

  &--third {
    stroke: #fcd329;
    animation: progress 1s ease-out forwards;
    z-index: 20;
  }
}

@keyframes progress {
	0% {
		stroke-dasharray: 0 100;
  }
}

.percent-holder__text-wrapper {
  position: absolute;
  top: 57px;
  left: 37px;
  text-align: center;
}

.percent-holder__text {
  margin: 0;
  font-size: 22px;
  font-weight: 600;
  line-height: 26px;
}

.percent-holder__large {
  position: relative;
  font-size: 42px;
  font-weight: 400;

  &::after {
    content: '%';
    position: absolute;
    font-size: 17px;
    top: -4px;
    right: -8px;
  }
}

.percent-holder__text--yellow {
  color: #fcd329;
  font-size: 16px;
}

.percent-holder__text-small {
  margin: 0;
  font-size: 11px;
  line-height: 13px;
  width: 145px;
  color: #9d9d9d;
}

.percent-holder__text--green {
  position: relative;
  padding-left: 20px;
  font-size: 18px;
}

.percent-holder__triangle {
  position: absolute;
  border: 8px solid transparent;
  border-bottom: 8px solid #70a84b;;
  left: 50px;
}

.percent-holder__line-wrapper {
  position: relative;
}

.percent-holder__first-line {
  position: absolute;
  width: 25px;
  height: 205px;
  border-top: 2px solid #b1b1b1;
  border-left: 2px solid #b1b1b1;
  border-radius: 9px 0;
  top: -140px;
  left: -35px;
}

.percent-holder__small-line {
  position: absolute;
  width: 15px;
  border-top: 2px solid #b1b1b1;
  top: 46px;
  left: -34px;
}

.percent-holder__cross {
  position: absolute;
  width: 12px;
  border-top: 2px solid #b1b1b1;
  top: 79px;
  left: -41px;

  &--vertical {
    transform: rotate(90deg);
  }
}

.percent-holder__last-line {
  position: absolute;
  width: 12px;
  height: 12px;
  border-left: 2px solid #b1b1b1;
  border-bottom: 2px solid #b1b1b1;
  border-radius: 0 9px;
  top: 90px;
  left: -36px;
}

