html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@import "blocks/page.scss";
@import "blocks/container.scss";
@import "blocks/percent-holder.scss";
@import "blocks/percent-list.scss";

@font-face {
  font-family: "Gotham Pro Regular";
  font-weight: 400;
  src: url("../fonts/GothamProRegular.woff") format("woff");
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Gotham Pro Medium";
  font-weight: 600;
  src: url("../fonts/GothamProMedium.woff") format("woff");
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Gotham Pro Bold";
  font-weight: 700;
  src: url("../fonts/GothamProBold.woff") format("woff");
  font-style: normal;
  font-display: swap;
}
