.percent-list {
  font-size: 13px;
  font-weight: 600;
  padding-left: 55px;
  padding: 20px 40px 10px 55px;
}

.percent-list__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
  position: relative;
}

.percent-list__percent {
  position: relative;
}

.percent-list__test {
  margin: 0;
}

.percent-list__wrapper {
  position: absolute;
  top: 3px;
  right: 97px;
}

.percent-list__quantity {
  margin: 0;
  line-height: 18px;
  font-size: 13px;
  position: relative;

  &--grey {
    color: #9d9d9d;
    padding-right: 17px;
    text-align: end;
  }
}

.percent-list__human {
  position: absolute;
  fill: #9d9d9d;
  width: 12px;
  height: 12px;
  top: 3px;
  right: 2px;
}

.percent-list__small-text {
  margin: 0;
  text-align: end;
  width: 88px;
  height: 36px;
  background-color: #f9f9f9;
  border: 3px solid #ececec;
  border-radius: 5px;
  padding: 6px 0;
}

.percent-list__rectangle {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: #70a74b;
  border-radius: 5px 0 0 0;
  z-index: 10;

  &--yellow {
    background-color: #fbd229;
  }

  &--red {
    background-color: #ed5243;
  }
}

.percent-list__lite-rectangle {
  position: absolute;
  width: 27px;
  height: 6px;
  background-color: #70a74b;
  opacity: 0.5;
  border-radius: 0 0 0 5px;
  top: 30px;
  z-index: 5;

  &--yellow {
    background-color: #fbd229;
  }

  &--red {
    background-color: #ed5243;
    width: 35px;
  }
}
